.nav-bar {
    display: flex;
    justify-content: space-between;
    white-space: nowrap;
    width: 100%;
    height: 6.25%;
    background-color: white;
    border-bottom: 1px solid #B6B6B6;
}

.back-block {
    align-self: center;
    font-size: 1.5em;

    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 5px;
}

.back-block div {
    margin: 1rem;
}

.my-lists-block {
    align-self: flex-start;

    display: flex;
    justify-content: right;
    align-items: flex-start;
    font-size: 1em;
    flex-wrap: nowrap;
    margin: 5px;
}

.my-lists-block div {
    margin-right: 0.5rem;
}