.body {
    align-items: flex-start;
    background-color: #f3f3f3;
    display: block;
    overflow: hidden;
    width: 100%;
    z-index: 3;
  }
  
  
.restaurant-box {
    height: 220px;
    width: 100%;
    position:relative;
}
  
.restaurant-image {
    height: 100%;
    object-fit: cover;
    width: 100%;
  }
  
  .top-bar {
    background-color: #c4c4c4ad;
    height: 45px;
    left: 1px;
    position: absolute;
    top: 0;
    width: 100%;
  }
  
  .bottom-bar {
    background-color: #c4c4c4ad;
    height: 45px;
    left: 0;
    position: absolute;
    top: 175px;
    width: 359px;
  }
  
  .title {
    height: 32px;
    left: 7px;
    letter-spacing: 0.84px;
    position: absolute;
    top: 5px;
    width: 319px;
  }
  
  .address {
    height: 21px;
    left: 10px;
    letter-spacing: 0.42px;
    position: absolute;
    top: 5px;
    left: 5px;
  }
  
  .opening-hours {
    height: 21px;
    left: 10px;
    letter-spacing: 0.42px;
    position: absolute;
    top: 20px;
    left: 5px;
  }
  
  .rating {
    height: 16px;
    letter-spacing: 0.42px;
    position: absolute;
    top: 20px;
    right: 5px;
  }