h3 {
    color: var(--black);
    font-family: var(--font-family-kumbh_sans);
    font-size: 1.5em;
    font-style: normal;
    font-weight: 600;
}

.Title-Section{
    display: flex;
    justify-content: space-between;
}

.Title-Section * {
    margin: 1rem;
}
.Details-Section-Outer {
    padding-left: 10px;
    padding-bottom: 20px;
}
.Details-Section-Inner{
    background: #EEEEEE;
    border-radius: 12px 0 0 12px;

    font-size: 0.7em;
    padding-top: 10px;
    padding-bottom: 10px;

    display: flex;
    justify-content: space-between;
}

li {
    margin: 6px;
}

.Call-Map-Icons{
    margin-right: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
