.search-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    white-space: nowrap;
    width: 100vw;
    height: 12vw;
    background-color: white;
    margin-bottom: 0.5vh;
}

.menu-button {
    height: 55%;
    width: 8%;
    margin: 2.5vh 0vw 2vh 4vw;
}
 
  
.searchBar {
    align-items: flex-end;
    flex-grow: 2;
    background-color: #E4E4E4;
    border-radius: 2vh;
    display: flex;
    height: 70%;
    margin: 1.5vh 2rem 1rem 3rem;
    padding: 1rem 1rem;
    display: flex;
    align-items: center;
}

.searchBarText{
    color: #B6B6B6;
    font-size: 2vh;
    margin-left: 2vw;
}
