.Carousel{
    width: 360px;
    cursor: grab;
    overflow: hidden;
}

.Inner-Carousel {
    width: 720px;
    display: flex;
}

/* .Inner-Carousel motion.div{
    pointer-events: none;
} */

.Main-Image{
    object-fit: cover;
    width : 360px;
    height: 360px;
    pointer-events: none;
}

.Side-Image-Grid{
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
}

.Side-Image-Grid li{
    box-sizing: border-box;
    margin: 0px;
    width: 120px;
    height: 120px;
    border: 1px solid #FEFEFE;
    pointer-events: none;
}

.Side-Images{
        object-fit: cover;
        margin: 0px;
        width: 120px;
        height: 120px
}